<template>
    <v-sheet class="x-history-list d-flex flex-column px-1">
        <slot name="default"></slot>

        <div v-if="!$slots['default']" class="d-flex flex-column px-1">
            <v-btn v-for="(item, $index) in options.items" :key="$index"
                :title="$t(item.hint)"
                :color="item.color"
                v-text="$t(item.label)"
                @click="onClick(item, $event)"
                small
                block
                depressed
                class="x-history-item text-none my-1">
            </v-btn>
        </div>
    </v-sheet>
</template>

<script>
export default {
    name: 'HistoryPanel',
    inheritAttrs: false,
    props: {
        options: {
            type: Object,
            default: () => {
                return {}
            }
        },
    },
    methods: {
        onClick(item, $event) {
            this.$emit('click', item, $event);

            if (item.click) {
                item.click(item, $event);
            }

            if (item.to) {
                if (typeof item.to === 'object') {
                    return this.$router.push(item.to);
                }
                if (item.to.indexOf('/') > -1) {
                    return this.$router.push({ path: item.to });
                }
                this.$router.push({ name: item.to });
            }
        },
    },
}
</script>

